html {
  height: 100%;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #2c3e50; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #3498db,
    #2c3e50
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  background-attachment: fixed !important;
}

.fullScreenHeight {
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		-webkit-animation: App-logo-spin infinite 20s linear;
		        animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

.PokeCard {
  background-color: gray;
  border-radius: 25px;
  padding: 20px;
  width: 225px;
  height: 370px;
  box-shadow: 7px 7px 3px -2px rgba(0, 0, 0, 0.75);
  margin-bottom: 20px;
  cursor: pointer;
}
.PokeCard-title {
  font-size: 1.5rem;
  margin-top: 0;
}

.PokeCard p {
  margin: 0.5rem;
}

.PokeCard p.PokeCard-p {
  margin-bottom: 16px;
}

.PokeCard img:hover {
  transform: scale(1.25);
  opacity: 0.7;
}

.PokeCard img {
  transition: all ease 500ms;
}

.PokeCard-image {
  border-radius: 1px;
  background-color: whitesmoke;
}

.PokeCard-grey {
  color: grey;
}

.PokeCard-flipper {
  padding: 30px;
}

.Pokedex-cards {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-left: auto;
  margin-right: auto;
  justify-content: space-evenly;
  padding: 30px;
}

.Pokedex-winner {
  color: #3eaf0e;
}

.Pokedex-loser {
  color: #da3c0b;
}

.Pokedex-header {
  margin: 0;
  padding: 20px;
}

.PokeGame-restart {
  margin: 1rem;
}

.PokeGame-loading-icon {
  color: aquamarine;
}

.fullScreenHeight {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

